import { jsonDeepCopy } from "util/util";
import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeUpdatePayload } from "sockapi/scope-updates";
import {
  SockAPITeamFinishedData,
  getIsTeamConditionSatisfied,
} from "sockapi/scope-specs/team-spec";
import { TeamSummaryState } from "sockapi/scope-specs/team-list-spec";
import { SockAPITeamAdminData } from "sockapi/scope-specs/hunt-admin-spec";
import { TeamPuzzleSockAPIScopeState } from "sockapi/scope-specs/team-spec";
import { CLIENT_SHARED_CUTSCENE_CHOICES } from "sockapi/scope-specs/team-cutscenes-spec";
import { HuntNotificationType } from "sockapi/client-notifications";
import { TeamProgressEventType } from "sockapi/team-progress";
import { SyncObjectType } from "sockapi/server-config";
import { ScopeEngine } from "sock-server/scope-engines";
import { BigBoardTeamState } from "sockapi/scope-specs/big-board-spec";
import { SERVER_HEALTH_CONFIG } from "sockapi/scope-specs/server-health-spec";
import { RequestContext } from "sock-server/exec-contexts";
import { SockServer } from "sock-server/SockServer";
import { GetInitTeamDataBackendResp } from "sock-server/backend-interface/BackendInterface";
import {
  BackendSlowSyncReqPacket,
  SyncLoopController,
  SyncTracker,
} from "sock-server/backend-interface/backend-sync";
import {
  FastSyncUpdate,
  FastSyncTracker,
  fastSyncUpdateToSyncPacket,
} from "sock-server/backend-interface/fast-sync";
import { SlowSyncTracker } from "sock-server/backend-interface/slow-sync";
import TeamProgressTracker from "sock-server/team-progress/TeamProgressTracker";
import {
  TeamProgressUpdateDiff,
  makeEmptyTeamProgressUpdateDiff,
} from "sock-server/team-progress/team-progress-diffs";
import { IntervalMetrics } from "sock-server/monitoring";
import { TeamAnswerableController } from "sock-server/server-controllers/team-answerable-controller";
import PUZZLE_CONFIG from "sock-server/puzzles/puzzle-config";
import { PuzzleType } from "sockapi/puzzle-data";

export class TeamController {
  /** Whether connections from this team are blocked. */
  isInactive: boolean;
  /** Whether the team should be exposed on leaderboards, etc. */
  isHidden: boolean;
  /** Whether the team is temporarily blocked for maintenance */
  isTempBlocked: boolean;

  /** The Django user ID, for impersonation. */
  userId: string;

  teamId: string;
  displayName: string;
  adminNotes: string;
  creationTime: number;
  members: string[];
  server: SockServer;

  /** Map of puzNames to fish puzzle controllers. */
  answerableControllers: Map<string, TeamAnswerableController>;

  /** puzNames of the puzzles viewed by the team. */
  views: Set<string>;

  /**
   * Tracker for team's progress, including solves and unlocks.
   * This only includes updates that have been persisted to
   * the backend.
   */
  progressTracker: TeamProgressTracker;

  /** The next hint ID to use, to ensure uniqueness. */
  nextHintId: number;

  numAuthsMetrics: IntervalMetrics;
  numRequestsMetrics: IntervalMetrics;

  /**
   * Tracking state for the fast backend sync.
   * By default, the data here is synced as soon as possible, as
   * there would be clients waiting on the sync.
   * If there is a request in flight, new data is synced immediately
   * after the request completes.
   * Unlike the periodic sync, pending changes here are broadcast and
   * applied only after the backend sync is complete.
   */
  fastSyncTracking: {
    syncLoopController: SyncLoopController;
    fastSyncTracker: FastSyncTracker;
  };

  /**
   * Tracking state for the slow backend sync.
   * The data here is only synced periodically.
   */
  slowSyncTracking: {
    slowSyncTracker: SlowSyncTracker;
  };

  constructor(
    teamId: string,
    initTeamData: GetInitTeamDataBackendResp,
    server: SockServer
  ) {
    const {
      userId,
      displayName,
      adminNotes = "",
      isInactive,
      isHidden,
      creationTime,
      members,
      views,
      teamProgress,
    } = initTeamData;
    this.userId = userId;
    this.teamId = teamId;
    this.displayName = displayName;
    this.adminNotes = adminNotes;
    this.creationTime = creationTime;
    this.members = members;
    this.server = server;

    this.answerableControllers = new Map();
    this.views = new Set(views);
    this.numAuthsMetrics = new IntervalMetrics(
      this.server.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );
    this.numRequestsMetrics = new IntervalMetrics(
      this.server.clock,
      SERVER_HEALTH_CONFIG.healthMonitoringIntervalMs
    );

    // Pass the initial state as an update and sync later during init
    // rather than initializing directly, so that we can invoke any
    // update triggers. This helps to ensure consistency and also handles
    // the initial unlocks.
    this.progressTracker = new TeamProgressTracker(
      () => this.server.puzzlesDB,
      () => this.server.customPuzzles,
      () => this.server.customPuzzlesCannedHints,
      this.server.clock,
      this.server.huntEndTime,
      jsonDeepCopy(teamProgress)
    );

    this.fastSyncTracking = {
      syncLoopController: new SyncLoopController(
        async () => await this.runFastSyncAsync()
      ),
      fastSyncTracker: new FastSyncTracker(
        this.teamId,
        new SyncTracker(
          this.server.config,
          this.server.clock,
          SyncObjectType.FAST,
          (dirtyState) => {
            this.server.dirtyStateTracker.update({
              teams: {
                [this.teamId]: {
                  fastSync: dirtyState,
                },
              },
            });
          }
        ),
        this.progressTracker
      ),
    };
    this.slowSyncTracking = {
      slowSyncTracker: new SlowSyncTracker(
        new SyncTracker(
          this.server.config,
          this.server.clock,
          SyncObjectType.MISC,
          (dirtyState) => {
            this.server.dirtyStateTracker.update({
              teams: {
                [this.teamId]: {
                  misc: dirtyState,
                },
              },
            });
          }
        )
      ),
    };

    this.isInactive = isInactive;
    this.isHidden = isHidden;
    this.isTempBlocked = false;

    this.nextHintId = 0;
  }

  /**
   * Separate init function to be called after the team controller
   * has been registered with the server.
   * This allows us to initialize engines.
   */
  init(initTeamData?: GetInitTeamDataBackendResp) {
    if (initTeamData !== undefined) {
      const { hints } = initTeamData;

      const hintsEngine = this.getHintsEngine();
      hintsEngine.applyUpdate({
        setNumGrantedTokens:
          this.progressTracker.view.getNumGrantedHintTokens(),
      });

      for (const [hintId, hint] of Object.entries(hints)) {
        hintsEngine.addHint(hintId, hint);
      }
    }
    this.initStats();
  }

  async initTeamProgressAsync() {
    await this.modifyTeamProgressAsync((progressTracker) => {
      // Ensure that we trigger updates even for new teams.
      progressTracker.applyUpdate({});
    });
  }

  /**
   * Init the team with the stats controller. This must always be
   * called after construction, once the team controller is registered
   * with the sock server.
   */
  private initStats() {
    this.server.statsController.registerTeam(this.teamId);
  }

  handleTeamUpdate(upd: SockAPIScopeUpdatePayload<ScopeType.TEAM>): void {
    const engine = this.server.scopeEnginesController.getEngineIfExists({
      type: ScopeType.TEAM,
      teamId: this.teamId,
    });
    if (engine !== undefined) engine.ctx.update(engine.ctx.scope, upd);
  }

  getAnswerableController(puzName: string): TeamAnswerableController {
    const existingController = this.answerableControllers.get(puzName);
    if (existingController !== undefined) return existingController;

    const controller = new TeamAnswerableController(
      this.teamId,
      puzName,
      this.server
    );
    this.answerableControllers.set(puzName, controller);
    return controller;
  }

  getAnswerableEngineIfExists(
    puzName: string
  ): ScopeEngine<ScopeType.TEAM_ANSWERABLE> | undefined {
    return this.server.scopeEnginesController.getEngineIfExists({
      type: ScopeType.TEAM_ANSWERABLE,
      teamId: this.teamId,
      puzName,
    });
  }

  getRoundEngine(roundId: string): ScopeEngine<ScopeType.ROUND> {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.ROUND,
      teamId: this.teamId,
      roundId,
    });
  }

  getCannedHintsEngine(): ScopeEngine<ScopeType.TEAM_CANNED_HINTS> {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.TEAM_CANNED_HINTS,
      teamId: this.teamId,
    });
  }

  getHintsEngine(): ScopeEngine<ScopeType.TEAM_HINTS> {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.TEAM_HINTS,
      teamId: this.teamId,
    });
  }

  getCollectiblesEngine(): ScopeEngine<ScopeType.TEAM_COLLECTIBLES> {
    return this.server.getOrCreateEngineOrThrow({
      type: ScopeType.TEAM_COLLECTIBLES,
      teamId: this.teamId,
    });
  }

  getPuzzleSolveTime(puzName: string): number | undefined {
    return this.progressTracker.getSolveTimes()[puzName];
  }

  getPuzzleUnlockTime(puzName: string): number | undefined {
    return this.progressTracker.getUnlockTimes()[puzName];
  }

  isPuzzleSolvedOrPendingSolved(puzName: string): boolean {
    return this.fastSyncTracking.fastSyncTracker.teamProgressTracker.hasSolvePuzzle(
      puzName
    );
  }

  isPuzzleSolved(puzName: string): boolean {
    return this.progressTracker.hasSolvePuzzle(puzName);
  }

  isPuzzleUnlocked(puzName: string): boolean {
    return this.progressTracker.hasUnlockPuzzle(puzName);
  }

  getClientAnswer(puzName: string): string | undefined {
    const solveEvent = this.progressTracker.view.puzzleSolves.get(puzName);
    if (solveEvent === undefined) return undefined;
    const {
      info: { answer: submittedAnswer },
    } = solveEvent;
    if (PUZZLE_CONFIG.rangeAnswers[puzName] !== undefined)
      return submittedAnswer;
    const { answer: canonicalAnswer } = this.server.getPuzzle(puzName);
    if (canonicalAnswer === null) return undefined;
    // If the puzzle was forced solve by admin, just reveal
    // the full answer spec.
    if (submittedAnswer === undefined) return canonicalAnswer;
    return submittedAnswer;
  }

  getClientPuzzleState(puzName: string): TeamPuzzleSockAPIScopeState {
    const puzData = this.server.getPuzzle(puzName);
    const unlockEvent = this.progressTracker.view.puzzleUnlocks.get(puzName);
    const solveEvent = this.progressTracker.view.puzzleSolves.get(puzName);
    if (unlockEvent === undefined)
      throw new Error(`tried to get client state for locked puzzle ${puzName}`);
    const {
      timestamp: unlockTime,
      info: { untitledPuzzleIndex },
    } = unlockEvent;
    const { timestamp: solveTime } = solveEvent ?? { info: {} };

    const {
      type,
      slug,
      displayName,
      answer,
      answerNormalization,
      roundId,
      cutsceneImages,
      region,
      transparentBackground,
      hasAudio,
      allowRotation,
      allowFlipping,
      cols,
      rows,
      isBigMeta,
      isMeta,
    } = puzData;
    return {
      type,
      puzName,
      slug,
      displayName,
      cols,
      rows,
      allowRotation,
      allowFlipping,
      hasAnswer: answer !== null ? true : undefined,
      answerNormalization,
      roundId,
      region,
      transparentBackground,
      hasAudio,
      answer: this.getClientAnswer(puzName),
      isNew: !this.views.has(puzName) ? true : undefined,
      unlockTime,
      solveTime,
      cutsceneImages,
      untitledPuzzleIndex,
      noCannedHints:
        this.server.getCannedHintsForPuzzle(puzName).length <= 0
          ? true
          : undefined,
      hasMultipleAnswers: (answer?.includes("/") ?? false) ? true : undefined,
      isBigMeta,
      isMeta,
      isFinalCutscene:
        puzName === PUZZLE_CONFIG.finalCutscenePuzName ? true : undefined,
    };
  }

  getLeaderboardNumSolves(): number {
    return this.progressTracker.view.displayedSolveCount;
  }

  getClientAdminData(): SockAPITeamAdminData {
    const {
      userId,
      displayName,
      adminNotes,
      isHidden,
      isInactive,
      isTempBlocked,
    } = this;
    return {
      userId,
      displayName,
      adminNotes,
      isHidden,
      isInactive,
      isTempBlocked,
    };
  }

  getHintsUsedMap(): { [puzName: string]: number } {
    return this.getHintsEngine().state.puzzleNumUsedTokens;
  }

  getClientBigBoardState(): BigBoardTeamState {
    const hintsUsedMap = this.getHintsUsedMap();
    return {
      numGrantedHints: this.getHintsEngine().state.numGrantedTokens,
      unlocks: Object.fromEntries(
        Object.values(
          this.progressTracker.getEventsForType(
            TeamProgressEventType.UNLOCK_PUZZLE
          )
        ).map(({ info: { puzName } }) => {
          return [
            puzName,
            {
              solveTime: this.getPuzzleSolveTime(puzName),
              numHintsUsed: hintsUsedMap[puzName],
              numWrongGuesses: this.answerableControllers
                .get(puzName)
                ?.getNumWrongGuesses(),
              approachability:
                this.progressTracker.getPuzzleApproachability(puzName) ??
                undefined,
            },
          ];
        })
      ),
    };
  }

  getLastSolveTime(): number | undefined {
    const solveTimes = this.progressTracker.getSolveTimes();
    return Object.keys(solveTimes).length === 0
      ? undefined
      : Math.max(
          0,
          ...Object.entries(solveTimes)
            .filter(
              ([puzName, timestamp]) =>
                this.server.getPuzzle(puzName).type === PuzzleType.PUZZLE &&
                timestamp <= this.server.huntEndTime
            )
            .map(([puzName, timestamp]) => timestamp)
        );
  }

  getCompletionTime(): number | undefined {
    const completionTime = this.getPuzzleSolveTime(
      PUZZLE_CONFIG.finalCutscenePuzName
    );
    if (
      completionTime === undefined ||
      completionTime >= this.server.huntEndTime
    )
      return undefined;
    return completionTime;
  }

  getTeamSummaryState(): TeamSummaryState {
    const { teamId, displayName, isHidden } = this;
    const collectibleIcons = this.getCollectiblesEngine().getIcons();
    return {
      teamId,
      displayName,
      numSolves: this.getLeaderboardNumSolves(),
      lastSolveTime: this.getLastSolveTime(),
      completionTime: this.getCompletionTime(),
      isHidden: isHidden ? true : undefined,
      collectibleIcons:
        collectibleIcons.length > 0 ? collectibleIcons : undefined,
    };
  }

  genHintId(): string {
    const hintId = `${this.server.sockServerId}__${this.teamId}__${this.nextHintId}`;
    this.nextHintId++;
    return hintId;
  }

  getIsFinished() {
    return this.progressTracker.hasSolvePuzzle(
      PUZZLE_CONFIG.finalCutscenePuzName
    );
  }

  getFinishedData(): SockAPITeamFinishedData | undefined {
    if (!this.server.getIsHuntOver() && !this.getIsFinished()) return undefined;
    const numCustomPuzzles = Object.values(this.server.customPuzzles).length;
    return {
      numSolveCountPuzzles: this.server.puzzlesDB.numSolveCountPuzzles,
      numCustomPuzzles,
    };
  }

  getLockedSolutions(): { [puzName: string]: true } {
    return Object.fromEntries(
      [...this.server.getPuzNames()]
        .filter(
          (puzName) =>
            this.progressTracker.hasSolvePuzzle(puzName) &&
            !this.progressTracker.isPuzzleSolutionReqsSatisfied(puzName)
        )
        .map((puzName) => [puzName, true])
    );
  }

  /** Condition checkers to reference from static data. */
  getIsConditionSatisfied(cond: string): boolean {
    return getIsTeamConditionSatisfied(cond, {
      cutsceneChoices: Object.fromEntries(
        this.progressTracker.view.cutsceneChoices
      ),
    });
  }

  /**
   * Actually run the fast sync.
   * Should only be used by the sync loop.
   * Returns if the sync is already complete (i.e. there are no sync
   * tasks in the buffers to run).
   */
  private async runFastSyncAsync(): Promise<boolean> {
    const { fastSyncTracker } = this.fastSyncTracking;

    const update = fastSyncTracker.startSync();
    if (update === null) return true;

    // Capture old state.
    const prevIsFinished = this.getIsFinished();
    const prevAccessibleRounds =
      this.progressTracker.view.getAccessibleRounds();

    const snapshot = fastSyncUpdateToSyncPacket(update, this.teamId);
    const { teamProgressUpd } = update;

    let didSyncSucceed = false;
    this.server.fastSyncMetrics.record();
    // If this errors out, the SyncLoopController will handle it.
    try {
      await this.server.syncDataThrottledAsync({
        teamState: {
          [this.teamId]: snapshot,
        },
      });
      didSyncSucceed = true;
    } finally {
      fastSyncTracker.endSync(didSyncSucceed);
    }

    // Apply the updates.
    const diff: TeamProgressUpdateDiff = (() => {
      if (teamProgressUpd === undefined)
        return makeEmptyTeamProgressUpdateDiff();
      return this.progressTracker.applyCompleteUpdateAndFlush(teamProgressUpd);
    })();

    const { solveRecords } = diff;
    const solveTimes = Object.fromEntries(
      Object.entries(solveRecords).map(([puzName, eventRecord]) => [
        puzName,
        eventRecord === null ? null : eventRecord.timestamp,
      ])
    );
    const answers = Object.fromEntries(
      Object.entries(solveRecords).flatMap(([puzName, eventRecord]) => {
        if (eventRecord === null) return [];
        const clientAnswer = this.getClientAnswer(puzName);
        if (clientAnswer === undefined) return [];
        return [[puzName, clientAnswer]];
      })
    );
    const cutsceneChoicesOpt = Object.fromEntries(
      Object.entries(diff.cutsceneChoices ?? {}).filter(
        ([keyframeId, choice]) =>
          CLIENT_SHARED_CUTSCENE_CHOICES.includes(keyframeId)
      )
    );
    const cutsceneChoices =
      Object.keys(cutsceneChoicesOpt).length > 0
        ? cutsceneChoicesOpt
        : undefined;

    // Broadcast the updates.
    const teamUnlockUpds = Object.fromEntries(
      Object.entries(diff.unlocks).map(([puzName, isUnlocked]) =>
        isUnlocked
          ? [puzName, this.getClientPuzzleState(puzName)]
          : [puzName, null]
      )
    );
    const hasUnlockUpdates = Object.keys(teamUnlockUpds).length > 0;
    const hasSolveUpdates = Object.keys(solveTimes).length > 0;
    const isNewlyFinished = !prevIsFinished && this.getIsFinished();
    const isNewlyUnfinished = prevIsFinished && !this.getIsFinished();
    const extraGuessesUpdates = Object.fromEntries(
      Object.keys(diff.diffPuzzleNumExtraGuesses ?? {}).map((puzName) => [
        puzName,
        this.progressTracker.view.puzzleNumExtraGuesses.get(puzName) ?? 0,
      ])
    );
    const accessibleRounds = this.progressTracker.view.getAccessibleRounds();
    const roundUpds = Object.fromEntries([
      ...[...prevAccessibleRounds]
        .filter((roundId) => !accessibleRounds.has(roundId))
        .map((roundId) => [roundId, null]),
      ...[...accessibleRounds]
        .filter((roundId) => !prevAccessibleRounds.has(roundId))
        .map((roundId) => [roundId, this.server.puzzlesDB.getRound(roundId)]),
    ]);
    this.handleTeamUpdate({
      solveTimes,
      answers,
      unlocks: teamUnlockUpds,
      rounds: roundUpds,
      // No need to be responsive here since we redeploy when hunt ends.
      // If the hunt is over, the client always has access to finished data.
      finishedData: this.server.getIsHuntOver()
        ? undefined
        : isNewlyFinished
          ? this.getFinishedData()
          : isNewlyUnfinished
            ? null
            : undefined,
      cutsceneChoices,
      lockedSolutions:
        hasUnlockUpdates || hasSolveUpdates
          ? this.getLockedSolutions()
          : undefined,
      extraGuesses:
        Object.keys(extraGuessesUpdates).length > 0
          ? extraGuessesUpdates
          : undefined,
    });
    for (const puzName of Object.keys(diff.diffPuzzleNumExtraGuesses ?? {})) {
      this.getAnswerableEngineIfExists(
        puzName
      )?.handleUpdateNumGuessesRemaining();
    }
    for (const puzName of Object.keys(this.progressTracker.getUnlockTimes())) {
      this.getAnswerableEngineIfExists(puzName)?.handleTeamProgressUpdate(diff);
    }

    this.getCannedHintsEngine().handleTeamProgressUpdate(diff);
    this.getHintsEngine().handleTeamProgressUpdate(diff);
    const affectedRounds = new Set(
      [
        ...Object.keys(diff.unlockRecords),
        // Background image might change when solves change.
        ...Object.keys(diff.solveRecords),
      ].flatMap((puzName) => {
        const { roundId } = this.server.getPuzzle(puzName);
        return roundId === undefined ? [] : [roundId];
      })
    );
    for (const roundId of affectedRounds) {
      this.getRoundEngine(roundId).handleTeamProgressUpdate(diff);
    }

    const hasCollectiblesUpdates =
      Object.keys(diff.diffCollectibleState ?? {}).length > 0;
    const collectiblesEngine = this.getCollectiblesEngine();
    if (hasCollectiblesUpdates) {
      collectiblesEngine.handleTeamProgressUpdate(diff);
    }
    if (hasSolveUpdates || hasCollectiblesUpdates) {
      this.server.handleTeamSummaryUpdate(this.teamId, {
        ...(hasSolveUpdates
          ? {
              numSolves: this.getLeaderboardNumSolves(),
              lastSolveTime: this.getLastSolveTime(),
              completionTime: this.getCompletionTime(),
            }
          : {}),
        ...(hasCollectiblesUpdates
          ? {
              collectibleIcons: collectiblesEngine.getIcons(),
            }
          : {}),
      });
    }

    const hintsUsedMap = this.getHintsUsedMap();
    this.server.bigBoardStateTracker.updateTeam(this.teamId, {
      // No need to worry about diffing here since this doesn't happen
      // too often, and we already diff in the tracker.
      unlocks: Object.fromEntries(
        Object.values(
          this.progressTracker.getEventsForType(
            TeamProgressEventType.UNLOCK_PUZZLE
          )
        ).map(({ info: { puzName } }) => [
          puzName,
          {
            solveTime: this.getPuzzleSolveTime(puzName),
            // Not sure if we would have reason to do un-unlocking hijinks
            // that could cause these to become non-zero on unlock,
            // but send them anyway just in case.
            numHintsUsed: hintsUsedMap[puzName],
            numWrongGuesses: this.answerableControllers
              .get(puzName)
              ?.getNumWrongGuesses(),
          },
        ])
      ),
    });

    // Broadcast answerable scope updates.
    for (const [puzName, solveAttemptsForPuzzle] of Object.entries(
      diff.solveAttempts
    )) {
      const { answer } = this.server.getPuzzle(puzName);
      if (answer === null) continue;
      this.getAnswerableController(puzName).handleUpdate(
        Object.fromEntries(
          Object.entries(solveAttemptsForPuzzle).map(
            ([eventId, eventRecord]) => {
              if (eventRecord === null) return [eventId, null];
              const {
                timestamp,
                info: { puzName, ...submissionInfo },
              } = eventRecord;
              return [
                eventId,
                {
                  timestamp,
                  ...submissionInfo,
                },
              ];
            }
          )
        )
      );
    }

    // This has to come after broadcasting the unlocks, to ensure
    // that teams have the puzzle global state to merge the
    // stats into.
    if (teamProgressUpd !== undefined)
      this.server.statsController.handleTeamUpdates(
        this.teamId,
        teamProgressUpd
      );

    // Broadcast the victory notification.
    if (isNewlyFinished) {
      this.server.broadcastNotifToTeam(this.teamId, {
        type: HuntNotificationType.VICTORY,
      });
    }

    return false;
  }

  async modifyTeamProgressAsync<TRet = void>(
    modifyFunc: (progressTracker: TeamProgressTracker) => TRet
  ): Promise<TRet> {
    const { fastSyncTracker, syncLoopController } = this.fastSyncTracking;
    const ret = fastSyncTracker.modifyTeamProgress(modifyFunc);
    await syncLoopController.triggerLoopAndWaitAsync();
    return ret;
  }

  setTeamInactive(isInactive: boolean) {
    if (isInactive === this.isInactive) return;
    this.isInactive = isInactive;
    this.mergeSlowSyncPacket({ isInactive });
    if (isInactive) {
      const teamConns = this.server.teamConns.get(this.teamId);
      if (teamConns !== undefined) {
        for (const conn of teamConns.values()) {
          conn.close();
        }
      }
    }
    this.server.handleHuntAdminUpdate({
      updTeams: {
        [this.teamId]: {
          isInactive,
        },
      },
    });
  }

  setTeamHidden(isHidden: boolean) {
    // Don't update stats if there is no change.
    if (isHidden === this.isHidden) return;
    this.isHidden = isHidden;
    this.mergeSlowSyncPacket({ isHidden });
    if (isHidden) {
      this.server.statsController.hideTeam(this.teamId);
    } else {
      this.server.statsController.unhideTeam(this.teamId);
    }
    this.server.handleTeamSummaryUpdate(this.teamId, {
      isHidden,
    });
  }

  setTeamTempBlocked(isTempBlocked: boolean) {
    if (isTempBlocked === this.isTempBlocked) return;
    this.isTempBlocked = isTempBlocked;
    if (isTempBlocked) {
      const teamConns = this.server.teamConns.get(this.teamId);
      if (teamConns !== undefined) {
        for (const conn of teamConns.values()) {
          if (!conn.isAdmin) {
            conn.sendErrCode(SockAPIErrorCode.TEAM_TEMP_BLOCKED);
            conn.close();
          }
        }
      }
    }
  }

  setDisplayName(displayName: string) {
    this.displayName = displayName;
    this.mergeSlowSyncPacket({ displayName });
    this.handleTeamUpdate({ displayName });
    this.server.handleTeamSummaryUpdate(this.teamId, { displayName });
  }

  setAdminNotes(adminNotes: string) {
    this.adminNotes = adminNotes;
    this.mergeSlowSyncPacket({ adminNotes });
    this.server.handleHuntAdminUpdate({
      updTeams: { [this.teamId]: { adminNotes } },
    });
  }

  handleSetMembers(members: string[]) {
    this.members = members;
    const engine = this.server.scopeEnginesController.getEngineIfExists({
      type: ScopeType.TEAM_MEMBERS,
      teamId: this.teamId,
    });
    if (engine !== undefined) engine.ctx.update(engine.ctx.scope, { members });
    this.server.statsController.setMembers(this.teamId);
  }

  viewPuzzle(ctx: RequestContext, puzName: string): void {
    if (!this.isPuzzleUnlocked(puzName))
      return ctx.setErrCode(SockAPIErrorCode.INVALID_PUZZLE);

    const timeNow = this.server.clock.genTimestamp();
    const viewTimes: { [puzName: string]: number } = {};
    if (!this.views.has(puzName)) viewTimes[puzName] = timeNow;

    for (const puzName of Object.keys(viewTimes)) {
      this.views.add(puzName);
      this.handleTeamUpdate({
        updPuzzles: {
          [puzName]: {
            isNew: false,
          },
        },
      });
    }
    this.mergeSlowSyncPacket({
      viewTimes,
    });
  }

  async mergeFastSyncUpdateAndWaitAsync(update: FastSyncUpdate) {
    const { fastSyncTracker, syncLoopController } = this.fastSyncTracking;
    fastSyncTracker.mergeUpdate(update);
    await syncLoopController.triggerLoopAndWaitAsync();
  }

  mergeSlowSyncPacket(packet: BackendSlowSyncReqPacket) {
    this.slowSyncTracking.slowSyncTracker.mergeUpdate(packet);
  }
}
