const getBgImage = (
  roundId: string,
  teamProgressView: TeamProgressView
): string => {
  switch (roundId) {
    case "intro": {
      const introProgress =
        teamProgressView.unlockGroupsSolveProgress.get("intro") ?? 0;
      if (introProgress <= 0) return "islandintro1.png";
      if (introProgress <= 2) return "islandintro2.png";
      if (introProgress <= 4) return "islandintro3.png";
      if (introProgress <= 6) return "islandintro4.png";
      return "islandintro5.png";
    }
    case "atoll":
      return "islandatoll.png";
    case "site":
      if (
        (teamProgressView.unlockGroupsSolveProgress.get("site-meta") ?? 0) <= 0
      )
        return "islandweb.png";
      return "islandweb2.png";
    case "crater":
      if (
        (teamProgressView.unlockGroupsSolveProgress.get("crater-meta") ?? 0) <=
        0
      )
        return "islandpieces.png";
      return "ship.png";
    case "crosswords":
      return "islandcrosswords.png";
    case "desert":
      return "islanddesert.png";
    case "endgame":
      if (teamProgressView.puzzleSolves.has(PUZZLE_CONFIG.finalCutscenePuzName))
        return "island-finale.png";
      return "islandnew.png";
    case "posthunt":
      return "kero-shadowed.png";
  }
  throw new Error(`unknown roundId: ${roundId}`);
};

class RoundEngine {
  ctx: ScopeEngineContext<ScopeType.ROUND>;
  teamController: TeamController;
  state: SockAPIScopeState<ScopeType.ROUND>;
  lastBgImage: string;

  constructor(
    ctx: ScopeEngineContext<ScopeType.ROUND>,
    teamController: TeamController,
    initState:
      | BackendScopeState<ScopeType.ROUND>
      | {
          movables: Record<
            string,
            Omit<Movable, "col" | "row" | "cols" | "rows"> & {
              x: number;
              y: number;
              height: number;
              width: number;
            }
          >;
        }
  ) {
    this.ctx = ctx;
    this.teamController = teamController;

    const firstMovable = Object.values(initState.movables)[0];
    if (firstMovable !== undefined && "x" in firstMovable) {
      // migrate
      const newMovables: Record<string, Movable> = {};
      for (const [id, movable] of Object.entries(initState.movables)) {
        let parCols, parRows;
        if (
          movable.type === MovableType.BOX ||
          movable.type === MovableType.PIECE
        ) {
          const { roundId } = this.ctx.server.getPuzzle(movable.id);
          const round = this.ctx.server.puzzlesDB.getRound(roundId!);
          parCols = round.gridCols;
          parRows = round.gridRows;
        } else {
          const { rows, cols, roundId } = this.ctx.server.getPuzzle(
            getBoxId(movable.id)
          );
          const { boxCols, boxRows } = this.ctx.server.puzzlesDB.getRound(
            roundId!
          );
          parCols = cols ?? boxCols;
          parRows = rows ?? boxRows;
        }
        newMovables[id] = {
          ...movable,
          cols: Math.round(movable.width * parCols),
          rows: Math.round(movable.height * parRows),
          col: Math.round(movable.x * parCols),
          row: Math.round(movable.y * parRows),
        };
      }
      initState.movables = newMovables;
    }

    // @ts-expect-error - migration
    this.state = {
      ...initState,
      bgImage: getBgImage(
        this.ctx.scope.roundId,
        teamController.progressTracker.view
      ),
    };
    this.lastBgImage = this.state.bgImage;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    // TODO: Only let teams who have unlocked this round access it
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  getState(): SockAPIScopeState<ScopeType.ROUND> {
    return this.state;
  }

  getLastOrder(): number {
    return Math.max(
      0,
      ...Object.values(this.state.movables).map((movable) => movable.order)
    );
  }

  getSlowSyncState(): BackendScopeState<ScopeType.ROUND> {
    return this.state;
  }

  reloadMovables(): void {
    const updates: Record<string, Movable> = {};
    const deletes: string[] = [];
    const updatedPieces = new Set<string>();
    const round = this.ctx.server.puzzlesDB.rounds.get(this.ctx.scope.roundId)!;
    for (const [movableId, movable] of Object.entries(this.state.movables)) {
      if (movable.type === "box" || movable.type === "piece") {
        const puzzle = this.ctx.server.getPuzzle(movableId);
        const cols =
          puzzle.cols ??
          (movable.type === "box" ? round.boxCols : round.pieceCols);
        const rows =
          puzzle.rows ??
          (movable.type === "box" ? round.boxRows : round.pieceRows);
        if (movable.cols !== cols || movable.rows !== rows) {
          updates[movableId] = {
            ...movable,
            cols,
            rows,
          };
          if (movable.type === "piece") {
            updatedPieces.add(movableId);
          }
        }
      }
    }
    for (const [movableId, movable] of Object.entries(this.state.movables)) {
      if (
        movable.type === "boxPiece" &&
        updatedPieces.has(getPieceId(movableId))
      ) {
        deletes.push(movableId);
      }
    }
    this.applyAndBroadcastUpdate({
      movables: updates,
      deleteMovables: deletes,
    });
  }

  handleTeamProgressUpdate(diff: TeamProgressUpdateDiff): void {
    const newMovables: Record<string, Movable> = {};
    const deleteMovables: string[] = [];
    const round = this.ctx.server.puzzlesDB.rounds.get(this.ctx.scope.roundId);
    if (round === undefined)
      throw new Error(`Round ${this.ctx.scope.roundId} not found`);
    const deletedPuzNames = new Set<string>();
    for (const [puzName, value] of Object.entries(diff.unlockRecords)) {
      if (value === null && puzName in this.state.movables) {
        deleteMovables.push(puzName);
        deletedPuzNames.add(puzName);
      }
      if (value !== null && !(puzName in this.state.movables)) {
        const movable = initialMovable(
          round,
          this.ctx.server.getPuzzle(puzName),
          this.getLastOrder() + 1
        );
        if (movable !== null) {
          newMovables[puzName] = movable;
        }
      }
    }
    for (const [movableId, movable] of Object.entries(this.state.movables)) {
      if (
        movable.type === "boxPiece" &&
        (deletedPuzNames.has(getBoxId(movable)) ||
          deletedPuzNames.has(getPieceId(movable)))
      ) {
        deleteMovables.push(movableId);
      }
    }
    const bgImage = getBgImage(
      this.ctx.scope.roundId,
      this.teamController.progressTracker.view
    );
    const needUpdateBgImage = this.state.bgImage !== bgImage;
    this.state.bgImage = bgImage;
    this.applyAndBroadcastUpdate({
      bgImage: needUpdateBgImage ? bgImage : undefined,
      movables: {},
      newMovables: newMovables ? newMovables : undefined,
      deleteMovables: deleteMovables ? deleteMovables : undefined,
    });
  }

  applyAndBroadcastUpdate(payload: SockAPIScopeUpdatePayload<ScopeType.ROUND>) {
    applyScopeUpdate(ScopeType.ROUND, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.ROUND>
  ) {
    switch (payload.type) {
      case RoundStepType.UPDATE: {
        const allValid = Object.entries(payload.movables ?? {}).every(
          ([movableId, movable]) => {
            if (
              movable.type === MovableType.PIECE ||
              movable.type === MovableType.BOX_PIECE
            ) {
              const pieceId = getPieceId(movableId);
              if (
                !this.ctx.server.getPuzzle(pieceId).allowRotation &&
                movable.rot !== 0
              ) {
                return false;
              }
            }
            return (
              // TODO: validate new movable?
              !this.state.movables[movableId] ||
              movable.version > this.state.movables[movableId].version
            );
          }
        );
        if (!allValid) {
          // TODO: better error here?
          return reqCtx.sendStepResp(this.ctx.scope, { success: false });
        }
        break;
      }
      default: {
        assertNever(payload.type);
      }
    }

    applyScopeUpdate(ScopeType.ROUND, this.state, payload);
    this.ctx.update(this.ctx.scope, payload);

    if (this.ctx.scope.roundId === "endgame") {
      const checks = Object.values(this.state.movables).flatMap((m) => {
        if (m.type !== MovableType.BOX_PIECE || !m.active) return [];
        return [`${getPieceId(m.id)}:${m.col}:${m.row}`];
      });
      const isCorrect = checks.sort().join("\n").trim() === ENDGAME_ANSWER;

      if (isCorrect) {
        await this.teamController.modifyTeamProgressAsync((progressTracker) => {
          progressTracker.addSolvePuzzle(PUZZLE_CONFIG.finalMetaPuzName);
        });
      }
    }

    return reqCtx.sendStepResp(this.ctx.scope, { success: true });
  }
}

export type RoundServerScopeSpec = {
  engine: RoundEngine;
};

const initialMovable = (
  round: RoundData,
  puzzle: PuzzleData,
  order = 1
): Movable | null => {
  if (puzzle.roundId !== round.roundId) {
    return null;
  }
  if (puzzle.type === PuzzleType.PUZZLE) {
    return {
      version: 0,
      order,
      col: puzzle.initialCol ?? 0,
      row: puzzle.initialRow ?? 0,
      rot: 0,
      type: MovableType.BOX,
      id: puzzle.puzName,
      cols: puzzle.cols ?? round.boxCols,
      rows: puzzle.rows ?? round.boxRows,
    } satisfies Box;
  }
  if (puzzle.type === PuzzleType.PIECE) {
    return {
      version: 0,
      order,
      col: puzzle.initialCol ?? 0,
      row: puzzle.initialRow ?? 0,
      rot: 0,
      type: MovableType.PIECE,
      id: puzzle.puzName,
      cols: puzzle.cols ?? round.pieceCols,
      rows: puzzle.rows ?? round.pieceRows,
    } satisfies Piece;
  }
  return null;
};

export const roundServerScopeHooks: ServerScopeHooks<ScopeType.ROUND> = {
  createEngine: ({ execCtx, engineCtx, initState }) => {
    const { controller, scope, server } = engineCtx;
    const teamController = controller.getTeamControllerIfExists(scope.teamId);
    if (teamController === null) {
      return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
    }

    const round = server.puzzlesDB.getRound(scope.roundId);
    return new RoundEngine(
      engineCtx,
      teamController,
      initState ?? {
        movables: (() =>
          Object.fromEntries(
            Array.from(
              teamController.progressTracker.view.puzzleUnlocks.keys(),
              (puzName, i) =>
                initialMovable(round, server.getPuzzle(puzName), i)
            )
              .filter((x) => x !== null)
              .map((m) => [m.id, m])
          ))(),
      }
    );
  },
  engineIdentityFunc: (engine) => engine,
};
