import { SockAPIErrorCode } from "sockapi/errors";
import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeStepPayload } from "sockapi/scope-steps";
import { TeamProgressEventType } from "sockapi/team-progress";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { RequestContext } from "sock-server/exec-contexts";
import { ClientConnection } from "sock-server/client-connections";
import { TeamController } from "sock-server/server-controllers/team-controller";
import { PuzzleType } from "sockapi/puzzle-data";

class TeamCutscenesEngine {
  ctx: ScopeEngineContext<ScopeType.TEAM_CUTSCENES>;
  teamController: TeamController;

  constructor(
    ctx: ScopeEngineContext<ScopeType.TEAM_CUTSCENES>,
    teamController: TeamController
  ) {
    this.ctx = ctx;
    this.teamController = teamController;
  }

  getPermissions(conn: ClientConnection) {
    return conn.teamId === this.ctx.scope.teamId
      ? ScopePermissions.FULL
      : ScopePermissions.ADMIN_ONLY;
  }

  async handleStepAsync(
    reqCtx: RequestContext,
    payload: SockAPIScopeStepPayload<ScopeType.TEAM_CUTSCENES>
  ) {
    const { server } = this.ctx;
    const { setCutsceneComplete, chooseDialogOption } = payload;
    if (setCutsceneComplete !== undefined) {
      const puzName = setCutsceneComplete;
      const puzData = server.tryGetPuzzle(puzName);
      if (puzData === null || puzData.type !== PuzzleType.STORY)
        return reqCtx.setErrCode(SockAPIErrorCode.INVALID_PUZZLE);
      await this.teamController.modifyTeamProgressAsync((progressTracker) => {
        if (!reqCtx.conn.isAdmin && !progressTracker.hasUnlockPuzzle(puzName))
          return reqCtx.setErrCode(SockAPIErrorCode.INVALID_PUZZLE);
        progressTracker.addSolvePuzzle(puzName);
        return false;
      });

      // Do not return here as we might have a dialog option
      // at the same time as a cutscene completion.
    }
    if (chooseDialogOption !== undefined) {
      const { keyframeId, index } = chooseDialogOption;
      const keyframeData = server.puzzlesDB.keyframesData.keyframes[keyframeId];
      if (keyframeData === undefined)
        return reqCtx.setErrCode(SockAPIErrorCode.INVALID_KEYFRAME);
      const numOptions = (keyframeData.dialogOptions ?? []).length;
      if (!(Number.isInteger(index) && index >= 0 && index < numOptions))
        return reqCtx.setBadRequest();

      return await this.teamController.modifyTeamProgressAsync(
        (progressTracker) => {
          const cond = keyframeData.dialogOptionsReqs?.[index];
          const isCondSatisfied =
            cond === undefined ||
            this.teamController.getIsConditionSatisfied(cond);
          // Don't error out here since this may be due to a
          // race condition.
          if (!isCondSatisfied)
            return reqCtx.sendStepResp(this.ctx.scope, { isDisallowed: true });

          const existingIndex =
            progressTracker.view.cutsceneChoices.get(keyframeId);
          if (existingIndex === undefined)
            progressTracker.addEvent(TeamProgressEventType.CUTSCENE_CHOICE, {
              keyframeId,
              index,
            });
          return reqCtx.sendStepResp(this.ctx.scope, {});
        }
      );
    }
    reqCtx.sendStepResp(this.ctx.scope, {});
  }
}

export type TeamCutscenesServerScopeSpec = {
  engine: TeamCutscenesEngine;
};

export const teamCutscenesServerScopeHooks: ServerScopeHooks<ScopeType.TEAM_CUTSCENES> =
  {
    createEngine: ({ execCtx, engineCtx }) => {
      const { controller, scope } = engineCtx;
      const teamController = controller.getTeamControllerIfExists(scope.teamId);
      if (teamController === null)
        return execCtx.setErrCodeAndReturn(SockAPIErrorCode.INVALID_TEAM, null);
      return new TeamCutscenesEngine(engineCtx, teamController);
    },
    engineIdentityFunc: (engine) => engine,
  };
