import { ScopeType } from "sockapi/scope-types";
import { SockAPIScopeState } from "sockapi/scope-state";
import { ServerScopeHooks } from "sock-server/server-scope-specs";
import {
  ScopePermissions,
  ScopeEngineContext,
} from "sock-server/scope-engines";
import { ClientConnection } from "sock-server/client-connections";

class PublicSubmissionsEngine {
  ctx: ScopeEngineContext<ScopeType.PUBLIC_SUBMISSIONS>;
  cachedState?: SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS>;

  constructor(ctx: ScopeEngineContext<ScopeType.PUBLIC_SUBMISSIONS>) {
    this.ctx = ctx;
  }

  getPermissions(conn: ClientConnection): ScopePermissions {
    return ScopePermissions.FULL;
  }

  private getStateNoCache(): SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> {
    const state: SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> = {
      submissions: {},
    };
    return state;
  }

  getState(): SockAPIScopeState<ScopeType.PUBLIC_SUBMISSIONS> {
    // We only ever need to compute this once, since
    // we'll only run this after all submissions are
    // finalized after hunt ends.
    this.cachedState ??= this.getStateNoCache();
    return this.cachedState;
  }
}

export type PublicSubmissionsServerScopeSpec = {
  engine: PublicSubmissionsEngine;
};

export const publicSubmissionsServerScopeHooks: ServerScopeHooks<ScopeType.PUBLIC_SUBMISSIONS> =
  {
    createEngine: ({ engineCtx }) => {
      return new PublicSubmissionsEngine(engineCtx);
    },
    engineIdentityFunc: (engine) => engine,
  };
