  "This isn't a puzzle, by the way.",
  "You really don't need to do this.",
  "But then again, neither did I.",
  "I hope that somebody is getting something out of this.",
  "But by getting something, I don't mean extractable data--it's not a puzzle, remember?",
  "For Pan's sake, what do you think, huh?",
  "I know, try guessing it again! It'll definitely work next time!",
  "Correct!!!",
  "...",
  ".....",
  "Drat, I was hoping that that would work.",
  "I'm guessing you're in this for the long haul, huh?",
  "Is there anything I could do to get you to leave me be?",
  "Still here, huh?",
  "It is what it is, I suppose.",
  "Well, fine. I've got a joke for you...",
  "... What do you call when two people meet up and hurt each other?",
  "PAIR FLAY!",
  "Heh heh heh. Did you like that?",
  "Well, *I* thought it was pretty clever.",
  "At this point, I assume you're trying to collect all these messages.",
  "Did you miss any opportunities?",
  "If you did, well, some things can't be taken back.",
  "There's always wrap-up though!",
  "Do you remember before when I said this wasn't a puzzle?",
