import { objectEntriesUnsafe, jsonDeepCopy } from "util/util";

import {
  doesPieceBelongTo,
  APPROACHABILITY_ICONS,
  PuzzleType,
  UnlockingInEdge,
  CannedHints,
  CustomPuzzles,
  PuzzleData,
} from "sockapi/puzzle-data";
import { SolveProgressInfo, SolveAttemptInfo } from "sockapi/solve-attempts";
import {
  TeamProgressEventType,
  TeamProgressEventReason,
  TeamProgressEventRecordExtraInfo,
  TeamProgressEventRecord,
  TeamProgressEventsForType,
  TeamProgress,
  createTeamProgressEventRecord,
  getTeamProgressEventsForType,
} from "sockapi/team-progress";
import {
  TeamProgressEventUpdateType,
  TeamProgressUpdate,
  TeamProgressUpdateForType,
  getTeamProgressUpdateForType,
  mergeTeamProgressUpdate,
  applyTeamProgressUpdateNoTrigger,
} from "sockapi/team-progress-updates";
import { PuzzlesDB } from "sock-server/puzzles/PuzzlesDB";
import { SockServerClock } from "sock-server/SockServer";
import { getCanonicalAnswers } from "sock-server/checkAnswer";
import TeamProgressView from "sock-server/team-progress/TeamProgressView";
import {
  TeamProgressUpdateDiff,
  computeTeamProgressUpdateDiff,
} from "sock-server/team-progress/team-progress-diffs";

// Constants relating to unlocking thresholds.
// Lint skipped because value currently unused.
// eslint-disable-next-line
const UNLOCKING_CONFIG: Record<string, unknown> = {};

type TeamProgressTriggerSources = {
  unlocks: TeamProgressEventRecord<TeamProgressEventType.UNLOCK_PUZZLE>[];
  solveAttempts: TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT>[];
};

export function convertTeamProgressToUpdate(
  teamProgress: TeamProgress,
  type: TeamProgressEventUpdateType
): TeamProgressUpdate {
  const { events } = teamProgress;
  return {
    events: Object.fromEntries(
      objectEntriesUnsafe(events).map(
        <TEventType extends TeamProgressEventType>([eventType, eventsForType]: [
          TEventType,
          TeamProgressEventsForType<TEventType>?,
        ]) => {
          const updateForType: TeamProgressUpdateForType<TEventType> = {};
          for (const key in eventsForType) {
            const timestamp = Number(key);
            updateForType[timestamp] = {
              type,
              eventRecord: eventsForType[timestamp],
            };
          }
          return [eventType, updateForType];
        }
      )
    ),
  };
}

class TeamProgressTracker {
  /** Ensure that we always use the most recent puzzlesDB from the server. */
  private getPuzzlesDB: () => PuzzlesDB;
  private getCustomPuzzlesFunc: () => CustomPuzzles;
  private getCustomPuzzlesCannedHintsFunc: () => CannedHints;
  private clock: SockServerClock;
  /** Only used for computing the leaderboard solve count. */
  huntEndTime: number;

  state: TeamProgress;
  /**
   * Updates reflected in `state` that have not been processed
   * for triggers, if any.
   */
  draftUpd?: TeamProgressUpdate;
  /**
   * Whether the state may be inconsistent. If unset, there may
   * be triggers that need to be processed that are not captured
   * by draftUpd.
   * Used only during initialization when database state may
   * contain partial updates.
   */
  isPossiblyInconsistent?: true;

  /**
   * Frequently accessed information about the events, updated
   * incrementally. This reflects all updates, including updates
   * that have not been processed for triggers.
   */
  view: TeamProgressView;

  constructor(
    getPuzzlesDB: () => PuzzlesDB,
    getCustomPuzzlesFunc: () => CustomPuzzles,
    getCustomPuzzlesCannedHintsFunc: () => CannedHints,
    clock: SockServerClock,
    huntEndTime: number,
    initState?: TeamProgress
  ) {
    this.getPuzzlesDB = getPuzzlesDB;
    this.getCustomPuzzlesFunc = getCustomPuzzlesFunc;
    this.getCustomPuzzlesCannedHintsFunc = getCustomPuzzlesCannedHintsFunc;
    this.clock = clock;
    this.huntEndTime = huntEndTime;
    this.state = {
      events: {},
    };
    this.view = new TeamProgressView();

    // Don't initialize the state directly, but rather construct
    // an initial update and apply it.
    // This ensures that we update view state and apply triggers
    // correctly.
    if (initState !== undefined) {
      this.applyUpdate(
        convertTeamProgressToUpdate(initState, TeamProgressEventUpdateType.ADD)
      );
    }
    delete this.draftUpd;
    this.isPossiblyInconsistent = true;
  }

  /**
   * Generates an event timestamp.
   * Ensures that all timestamps generated by this function are unique.
   */
  genTimestamp(): number {
    return this.clock.genTimestamp();
  }

  getEventsForType<TEventType extends TeamProgressEventType>(
    eventType: TEventType
  ): TeamProgressEventsForType<TEventType> {
    return getTeamProgressEventsForType(this.state, eventType);
  }

  getPuzzleSolveAttempts() {
    return this.view.puzzleSolveAttempts;
  }

  getSolveAttemptRecordsForPuzzle(
    puzName: string
  ): Iterable<TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT>> {
    return this.getPuzzleSolveAttempts().l1Get(puzName).values();
  }

  getPuzzleSolves(): ReadonlyMap<
    string,
    TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT>
  > {
    return this.view.puzzleSolves;
  }

  getSolves(): Iterable<string> {
    return this.getPuzzleSolves().keys();
  }

  getUnlockTimes(): Readonly<{ [puzName: string]: number }> {
    return this.view.puzzleUnlockTimes;
  }

  getSolveTimes(): Readonly<{ [puzName: string]: number }> {
    return this.view.puzzleSolveTimes;
  }

  clone() {
    if (this.draftUpd !== undefined)
      throw new Error(
        "Cannot clone a team progress tracker with pending updates"
      );
    return new TeamProgressTracker(
      this.getPuzzlesDB,
      this.getCustomPuzzlesFunc,
      this.getCustomPuzzlesCannedHintsFunc,
      this.clock,
      this.huntEndTime,
      jsonDeepCopy(this.state)
    );
  }

  getEventRecord<TEventType extends TeamProgressEventType>(
    type: TEventType,
    timestamp: number
  ): TeamProgressEventRecord<TEventType> | undefined {
    return this.getEventsForType(type)[timestamp];
  }

  hasSolvePuzzle(puzName: string): boolean {
    return this.getPuzzleSolves().get(puzName) !== undefined;
  }

  hasUnlockPuzzle(puzName: string): boolean {
    return this.view.puzzleUnlocks.get(puzName) !== undefined;
  }

  getUnlockGroupsUnlockProgress(unlockGroupId: string): number {
    const unlockGroupsUnlockPrefix = "unlock__";
    if (unlockGroupId.startsWith(unlockGroupsUnlockPrefix))
      return this.hasUnlockPuzzle(
        unlockGroupId.slice(unlockGroupsUnlockPrefix.length)
      )
        ? 1
        : 0;
    return this.view.unlockGroupsUnlockProgress.get(unlockGroupId) ?? 0;
  }

  getUnlockGroupsSolveProgress(unlockGroupId: string): number {
    const unlockGroupsSolvePrefix = "solve__";
    if (unlockGroupId.startsWith(unlockGroupsSolvePrefix))
      return this.hasSolvePuzzle(
        unlockGroupId.slice(unlockGroupsSolvePrefix.length)
      )
        ? 1
        : 0;
    return this.view.unlockGroupsSolveProgress.get(unlockGroupId) ?? 0;
  }

  areInEdgesSatisfied(inEdges: UnlockingInEdge[]): boolean {
    return inEdges.every(({ unlockGroupId, val = 1, unlockVal }) => {
      if (
        unlockVal !== undefined &&
        unlockVal !== -1 &&
        this.getUnlockGroupsUnlockProgress(unlockGroupId) >= unlockVal
      ) {
        return true;
      }
      if (
        val !== undefined &&
        val !== -1 &&
        this.getUnlockGroupsSolveProgress(unlockGroupId) >= val
      ) {
        return true;
      }
      return false;
    });
  }

  isPuzzleUnlockReqsSatisfied(puzName: string): boolean {
    const puzData = this.getPuzzlesDB().getPuzzle(
      puzName,
      this.getCustomPuzzlesFunc()
    );
    const { unlockInEdges } = puzData;

    return this.areInEdgesSatisfied(unlockInEdges);
  }

  isPuzzleSolutionReqsSatisfied(puzName: string): boolean {
    const puzData = this.getPuzzlesDB().getPuzzle(
      puzName,
      this.getCustomPuzzlesFunc()
    );
    const { solutionInEdges = [] } = puzData;

    return this.areInEdgesSatisfied(solutionInEdges);
  }

  /** How approachable is this puzzle, given the state of the team's unlocks? */
  getPuzzleApproachability(puzName: string): number | null {
    const puzData = this.getPuzzlesDB().getPuzzle(
      puzName,
      this.getCustomPuzzlesFunc()
    );
    return puzData.difficultyThresholds
      ? puzData.difficultyThresholds.filter(
          ({ unlockGroupId, val = 1 }) =>
            (this.getUnlockGroupsSolveProgress(unlockGroupId) ?? 0) >= val
        ).length
      : null;
  }

  getSolveProgress(current: PuzzleData): SolveProgressInfo {
    const pieceUnlocks = (() => {
      if (current.type !== PuzzleType.PUZZLE) return;
      const pieceUnlocks: [number, number] = [0, 0];
      for (const other of this.getPuzzlesDB().getPuzzles(
        this.getCustomPuzzlesFunc()
      )) {
        if (other.type !== PuzzleType.PIECE) continue;
        if (!doesPieceBelongTo(other, current)) continue;
        pieceUnlocks[1]++;
        if (!this.hasUnlockPuzzle(other.puzName)) continue;
        pieceUnlocks[0]++;
      }
      if (pieceUnlocks[1] === 0) return;
      return pieceUnlocks;
    })();
    const feederSolves = (() => {
      if (current.type !== PuzzleType.PUZZLE) return;
      if (!current.isMeta) return;
      const feederSolves: [number, number] = [0, 0];
      for (const other of this.getPuzzlesDB().getPuzzles(
        this.getCustomPuzzlesFunc()
      )) {
        if (other.type !== PuzzleType.PUZZLE) continue;
        if (other.puzName === current.puzName) continue;
        if (other.roundId !== current.roundId) continue;
        feederSolves[1]++;
        if (!this.hasSolvePuzzle(other.puzName)) continue;
        feederSolves[0]++;
      }
      return feederSolves;
    })();
    const a13y = this.getPuzzleApproachability(current.puzName);
    const summaries = [
      pieceUnlocks === undefined ? [] : [`:${pieceUnlocks.join("/")}`],
      feederSolves === undefined ? [] : [`:${feederSolves.join("/")}`],
      a13y === null ? [] : [APPROACHABILITY_ICONS[a13y]],
    ].flat();
    return {
      pieceUnlocks,
      feederSolves,
      approachability: a13y ?? undefined,
      progressSummary: summaries.length > 0 ? summaries.join(" ") : undefined,
    };
  }

  /**
   * If an event is added and then immediately removed, it would
   * remain in the update as a remove update.
   * This function removes such zombie updates to make it easier
   * to compute diffs.
   */
  private filterOutRedundantUpdates(
    upd: TeamProgressUpdate
  ): TeamProgressUpdate {
    const { events, ...otherUpdates } = upd;
    if (events === undefined) return otherUpdates;
    return {
      events: Object.fromEntries(
        objectEntriesUnsafe(events).map(
          <TEventType extends TeamProgressEventType>([eventType, updsForType]: [
            TEventType,
            TeamProgressUpdateForType<TEventType>?,
          ]) => {
            const updateForType: TeamProgressUpdateForType<TEventType> = {};
            for (const key in updsForType) {
              const timestamp = Number(key);
              const upd = updsForType[timestamp];
              if (
                upd.type !== TeamProgressEventUpdateType.REMOVE ||
                this.state.events[eventType]?.[timestamp] !== undefined
              )
                updateForType[timestamp] = upd;
            }
            return [eventType, updateForType];
          }
        )
      ),
      ...otherUpdates,
    };
  }

  applyUpdate(rawUpd: TeamProgressUpdate): TeamProgressUpdateDiff {
    const upd = this.filterOutRedundantUpdates(rawUpd);
    const diff = computeTeamProgressUpdateDiff(this.getPuzzlesDB(), this, upd);

    if (this.draftUpd === undefined) this.draftUpd = upd;
    else mergeTeamProgressUpdate(this.draftUpd, upd);
    applyTeamProgressUpdateNoTrigger(this.state, upd);

    for (const updsForType of Object.values(upd.events ?? {})) {
      for (const upd of Object.values(updsForType)) {
        if (upd.type === TeamProgressEventUpdateType.REMOVE) continue;
        this.clock.bumpLatestTime(upd.eventRecord.timestamp);
      }
    }

    this.view.handleDiff(diff);
    return diff;
  }

  applyCompleteUpdateAndFlush(upd: TeamProgressUpdate): TeamProgressUpdateDiff {
    if (!(upd.isComplete ?? false)) throw new Error("update not complete");
    const diff = this.applyUpdate(upd);
    this.triggerUpdatesAndFlush();
    return diff;
  }

  private addEventRecord(eventRecord: TeamProgressEventRecord): void {
    const { type, timestamp } = eventRecord;
    this.applyUpdate({
      events: {
        [type]: {
          [timestamp]: {
            type: TeamProgressEventUpdateType.ADD,
            eventRecord,
          },
        },
      },
    });
  }

  addEvent<TEventType extends TeamProgressEventType>(
    type: TEventType,
    info: TeamProgressEventRecordExtraInfo<TEventType>
  ): TeamProgressEventRecord<TEventType> {
    const timestamp = this.genTimestamp();
    const eventRecord = createTeamProgressEventRecord(type, timestamp, info);
    this.addEventRecord(eventRecord);
    return eventRecord;
  }

  addSolveAttempt(
    puzName: string,
    info: Omit<SolveAttemptInfo, "timestamp">
  ): TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null {
    const { isCorrect, isWrong, answer } = info;
    if ((isCorrect ?? false) && this.hasSolvePuzzle(puzName)) return null;
    const puzData = this.getPuzzlesDB().getPuzzle(
      puzName,
      this.getCustomPuzzlesFunc()
    );
    const canonicalAnswer = getCanonicalAnswers(puzData)?.join(" / ") ?? null;
    return this.addEvent(TeamProgressEventType.SOLVE_ATTEMPT, {
      puzName,
      ...info,
      isCorrect: isCorrect ? true : undefined,
      isWrong: isWrong ? true : undefined,
      answer: answer ?? canonicalAnswer ?? undefined,
      ...this.getSolveProgress(puzData),
    });
  }

  addSolvePuzzle(
    puzName: string
  ): TeamProgressEventRecord<TeamProgressEventType.SOLVE_ATTEMPT> | null {
    return this.addSolveAttempt(puzName, { isCorrect: true });
  }

  addUnlockPuzzle(
    puzName: string
  ): TeamProgressEventRecord<TeamProgressEventType.UNLOCK_PUZZLE> | null {
    if (this.hasUnlockPuzzle(puzName)) return null;
    const { displayName } = this.getPuzzlesDB().getPuzzle(
      puzName,
      this.getCustomPuzzlesFunc()
    );
    const untitledPuzzleIndex =
      displayName === "" ? this.view.nextUntitledPuzzleIndex : undefined;
    return this.addEvent(TeamProgressEventType.UNLOCK_PUZZLE, {
      puzName,
      untitledPuzzleIndex,
    });
  }

  overridePuzzleNumCannedHintsFound(
    puzName: string,
    newNum: number,
    reason: TeamProgressEventReason
  ) {
    const maxNum = this.getPuzzlesDB().getCannedHintsForPuzzle(
      puzName,
      this.getCustomPuzzlesCannedHintsFunc()
    ).length;
    const currNum = this.view.puzzleNumTotalCannedHints.get(puzName) ?? 0;
    const diffNum = Math.min(newNum, maxNum) - currNum;
    if (diffNum === 0) return;
    this.addEvent(TeamProgressEventType.GET_CANNED_HINT, {
      puzName,
      num: diffNum,
      reason,
    });
  }

  removeEvent(type: TeamProgressEventType, timestamp: number): void {
    const eventRecord = this.getEventRecord(type, timestamp);
    if (eventRecord === undefined) return;
    this.applyUpdate({
      events: {
        [type]: {
          [timestamp]: {
            type: TeamProgressEventUpdateType.REMOVE,
            eventRecord,
          },
        },
      },
    });
  }

  removeEventRecord(eventRecord: TeamProgressEventRecord): void {
    this.removeEvent(eventRecord.type, eventRecord.timestamp);
  }

  removeSolvePuzzle(puzName: string): void {
    for (const eventRecord of this.getPuzzleSolveAttempts()
      .l1Get(puzName)
      .values())
      this.removeEventRecord(eventRecord);
  }

  removeUnlockPuzzle(puzName: string): void {
    // If we're removing an unlock, remove the solve too.
    this.removeSolvePuzzle(puzName);
    const eventRecord = this.view.puzzleUnlocks.get(puzName);
    if (eventRecord !== undefined) this.removeEventRecord(eventRecord);
  }

  private triggerUpdatesFromSources(
    triggerSources: TeamProgressTriggerSources
  ) {
    const puzzlesDB = this.getPuzzlesDB();

    for (const eventRecord of triggerSources.solveAttempts) {
      const {
        info: { puzName },
      } = eventRecord;
      // All puzzles with solve attempts, including those requested
      // to be solved or whose solved were auto-triggered, should
      // be unlocked.
      this.addUnlockPuzzle(puzName);
    }

    // Returns if any new puzzles were unlocked.
    const autoUnlockAccessiblePuzzles = (puzzles: PuzzleData[]): boolean => {
      const puzzlesToUnlock = puzzles.filter(
        ({ puzName, isAutoUnlocked = false }) =>
          isAutoUnlocked &&
          this.isPuzzleUnlockReqsSatisfied(puzName) &&
          !this.hasUnlockPuzzle(puzName)
      );
      if (puzzlesToUnlock.length <= 0) return false;
      // GPH 2025 HACK: Sort by puzName, which is randomly generated
      // for pieces. This prevents auto-assigned piece unlock numbers
      // from bunching up by puzzle.
      puzzlesToUnlock.sort((a, b) => {
        // Make cutscenes unlock first.
        const aIsStory = a.type === PuzzleType.STORY ? 1 : 0;
        const bIsStory = b.type === PuzzleType.STORY ? 1 : 0;
        if (aIsStory !== bIsStory) return bIsStory - aIsStory;
        if (aIsStory) return a.order - b.order;
        return a.puzName.localeCompare(b.puzName);
      });
      for (const { puzName } of puzzlesToUnlock) {
        this.addUnlockPuzzle(puzName);
      }
      return true;
    };

    // First pass of puzzle unlocking.
    let areNewPuzzlesUnlocked = autoUnlockAccessiblePuzzles([
      ...puzzlesDB.getPuzzles(this.getCustomPuzzlesFunc()),
    ]);

    // Handle any puzzles newly unlocked, for puzzles that can be unlocked
    // based on other puzzles being unlocked..
    while (areNewPuzzlesUnlocked) {
      areNewPuzzlesUnlocked = autoUnlockAccessiblePuzzles(
        puzzlesDB.puzzlesWithUnlockBasedUnlockReqs
      );
    }
  }

  triggerUpdatesAndFlush(): TeamProgressUpdate | null {
    const triggerSources: TeamProgressTriggerSources | null = (() => {
      if (this.isPossiblyInconsistent ?? false) {
        return {
          unlocks: Object.values(
            this.getEventsForType(TeamProgressEventType.UNLOCK_PUZZLE)
          ),
          solveAttempts: Object.values(
            this.getEventsForType(TeamProgressEventType.SOLVE_ATTEMPT)
          ),
        };
      }

      const { draftUpd } = this;
      if (draftUpd === undefined) return null;

      // If the update is already complete, no need to trigger
      // further updates.
      if (draftUpd.isComplete ?? false) return null;

      return {
        unlocks: Object.values(
          getTeamProgressUpdateForType(
            draftUpd,
            TeamProgressEventType.UNLOCK_PUZZLE
          )
        )
          .filter((upd) => upd.type === TeamProgressEventUpdateType.ADD)
          .map(({ eventRecord }) => eventRecord),
        solveAttempts: Object.values(
          getTeamProgressUpdateForType(
            draftUpd,
            TeamProgressEventType.SOLVE_ATTEMPT
          )
        )
          .filter((upd) => upd.type === TeamProgressEventUpdateType.ADD)
          .map(({ eventRecord }) => eventRecord),
      };
    })();

    if (triggerSources !== null) this.triggerUpdatesFromSources(triggerSources);

    const finalUpd = ((): TeamProgressUpdate | null => {
      if (this.draftUpd === undefined) return null;
      // Filter out redundant updates to prevent unnecessary syncs.
      if (Object.keys(this.draftUpd).length === 0) return null;
      return this.draftUpd;
    })();
    delete this.draftUpd;
    if (finalUpd !== null) finalUpd.isComplete = true;
    delete this.isPossiblyInconsistent;
    return finalUpd;
  }

  /** Create bootstrap progress when setting up the mock backend. */
  makeBootstrapProgress({
    unlockAllPuzzles,
    solveAllPuzzles,
  }: {
    unlockAllPuzzles?: boolean;
    solveAllPuzzles?: boolean;
  }): void {
    if ((unlockAllPuzzles ?? false) || (solveAllPuzzles ?? false)) {
      for (const { puzName } of this.getPuzzlesDB().getPuzzles(
        this.getCustomPuzzlesFunc()
      )) {
        this.addUnlockPuzzle(puzName);
        if (solveAllPuzzles ?? false) this.addSolvePuzzle(puzName);
      }
    }
  }
}

export default TeamProgressTracker;
