import {
  PuzzleData,
  AllPuzzleContent,
  PuzzleMessagesData,
  CannedHints,
  KeyframesData,
  CollectiblesData,
  PuzzlesDBData,
  CustomPuzzles,
  PuzzleType,
  RoundData,
} from "sockapi/puzzle-data";

export class PuzzlesDB {
  private puzzles: ReadonlyMap<string, PuzzleData>;
  rounds: ReadonlyMap<string, RoundData>;
  content: AllPuzzleContent;
  puzzleMessages: PuzzleMessagesData;
  private cannedHints: Readonly<CannedHints>;
  keyframesData: KeyframesData;
  collectiblesData: CollectiblesData;

  numSolveCountPuzzles: number;
  puzzlesWithUnlockBasedUnlockReqs: PuzzleData[];

  constructor({
    allPuzzles,
    allPuzzleContent,
    puzzleMessages,
    cannedHints,
    keyframesData,
    collectiblesData,
  }: PuzzlesDBData) {
    const { puzzles, rounds } = allPuzzles;
    this.puzzles = new Map(
      puzzles.map((puzData) => [puzData.puzName, puzData])
    );
    this.rounds = new Map(rounds.map((round) => [round.roundId, round]));
    this.content = allPuzzleContent;
    this.puzzleMessages = puzzleMessages;
    this.cannedHints = cannedHints;
    this.keyframesData = keyframesData;
    this.collectiblesData = collectiblesData;
    this.numSolveCountPuzzles = [...this.puzzles.values()].filter(
      (puzData) => puzData.type === PuzzleType.PUZZLE
    ).length;
    this.puzzlesWithUnlockBasedUnlockReqs = [...this.puzzles.values()].filter(
      ({ unlockInEdges }) =>
        unlockInEdges.some(({ unlockVal }) => unlockVal !== undefined)
    );
  }

  *getPuzNames(customPuzzles: CustomPuzzles): Iterable<string> {
    yield* this.puzzles.keys();
    yield* Object.keys(customPuzzles);
  }

  *getPuzzles(customPuzzles: CustomPuzzles): Iterable<PuzzleData> {
    yield* this.puzzles.values();
    yield* Object.values(customPuzzles);
  }

  getCannedHintsForPuzzle(
    puzName: string,
    customPuzzlesCannedHints: CannedHints
  ): string[] {
    return this.cannedHints[puzName] ?? customPuzzlesCannedHints[puzName] ?? [];
  }

  *getCannedHints(
    customPuzzlesCannedHints: CannedHints
  ): Iterable<[string, string[]]> {
    yield* Object.entries(this.cannedHints);
    yield* Object.entries(customPuzzlesCannedHints);
  }

  tryGetPuzzle(
    puzName: string,
    customPuzzles: CustomPuzzles
  ): PuzzleData | null {
    return this.puzzles.get(puzName) ?? customPuzzles[puzName] ?? null;
  }

  getPuzzle(puzName: string, customPuzzles: CustomPuzzles): PuzzleData {
    const puzData = this.tryGetPuzzle(puzName, customPuzzles);
    if (puzData === null)
      throw new Error(`could not resolve puzzle ${puzName}`);
    return puzData;
  }

  getRound(roundId: string): RoundData {
    const round = this.rounds.get(roundId);
    if (round === undefined)
      throw new Error(`could not resolve round ${roundId}`);
    return round;
  }

  /** Get the canonical answer for an answerable. */
  getAnswerableCanonicalAnswer(
    puzName: string,
    customPuzzles: CustomPuzzles
  ): string {
    const { answer } = this.getPuzzle(puzName, customPuzzles);
    if (answer === null) throw new Error(`${puzName} does not have an answer`);
    return answer;
  }

  isPuzzle(puzName: string): boolean {
    const puzData = this.tryGetPuzzle(puzName, {});
    // Custom puzzles are always micropuzzles.
    if (puzData === null) return true;
    return puzData.type === PuzzleType.PUZZLE;
  }
}

export const makeEmptyPuzzlesDB = (): PuzzlesDB => {
  return new PuzzlesDB({
    allPuzzles: { rounds: [], puzzles: [] },
    allPuzzleContent: { puzzles: {}, solutions: {} },
    puzzleMessages: {},
    cannedHints: {},
    keyframesData: {
      keyframes: {},
      firstKeyframeIds: {},
    },
    collectiblesData: {},
  });
};
